import React from 'react';
import { Helmet } from 'react-helmet';
import { generateHeadTags } from 'react-seo-tools/lib/generateHeadTags';

export default function SEO({ title, description,keywords }) {
    return (
        <Helmet>
            {generateHeadTags({
                title,
                description,
                openGraph: {
                    'type': 'article',
                    'title': `${title}`,
                    'image': 'https://sojiare/soji.png',
                    'article:author': 'SojiAre.com',
                    'article:tag': ['sojiare.com', 'sojiare.com/company'],
                    'article:published_time': '2022-04-26',
                },
            })}
            <meta name='keywords' content={keywords} />
        </Helmet>
    );
}
