import React, { useState } from 'react';
import styled from 'styled-components';
import { FaRegStar, FaStar, FaStarHalf } from 'react-icons/fa';
import ReactStars from 'react-rating-stars-component';
import { Input } from '@components/libs/Forms';
import { colors } from '@components/typo';
import Button from '@components/libs/Button';
import SojiBaseInit from '@modules/SojiBase';
import { useAlertState } from '@components/libs/Notify/useAlertState';

const Container = styled.div`
    .add_review_modal {
        width: 500px;
        min-height: 385px;
        max-height: 95vh;
        box-shadow: 3px 12px 24px 13px rgba(0, 0, 0, 0.03);
        background-color: #fff;
        border-radius: 10px;
        padding: 10px;
    }

    @media only screen and (max-width: 767px) {
        .add_review_modal {
            width: 95vw;
        }
    }
`;

const Title = styled.div`
    font-size: 16px;
    font-weight: 700;
    margin-top: 10px;
`;

const ButtonContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
`;

const SubmitButton = styled.div`
    cursor: pointer;
    width: 200px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: ${({ cancel }) => (cancel ? colors.grey : colors.defaultColor)};
    color: ${({ cancel }) => (cancel ? colors.textColor : '#fff')};
    border-radius: 6px;
`;

const SendButton = styled(Button)`
    width: 200px;
    height: 40px;
    border-radius: 6px;
`;

export default function AddReview({ data, onRequestClose }) {
    const alertState = useAlertState();

    const [state, setState] = useState({
        reviewComment: '',
        reviewTitle: '',
        reviewStar: 0,
        loading: false,
        error: false
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const ratingChanged = (newRating) => {
        stateHandler('reviewStar', newRating);
    };

    const reviewAction = async () => {
        stateHandler('loading', true);
        const SojiBase = await SojiBaseInit;
        const payload = {
            companyID: data.id,
            reviewScore: state.reviewStar,
            reviewComment: state.reviewComment,
            reviewTitle: state.reviewTitle,
        };
        const getter = await SojiBase.post(`v1/company/review_company`, payload);
        getter.on('static', (response) => {
            stateHandler('loading', false);   
            if (response.response.status != "200") {
                stateHandler('error', response.response.data);
            } else {
                onRequestClose();
                alertState.set({
                    state: true,
                    title: 'Notification',
                    desc: response.response.data,
                    type: 'success',
                    onClose: () => window.location.reload()
                });
            }
        });
    };

    return (
        <Container>
            <div className="add_review_modal">
                <Title>Rate your recent experience</Title>
                <ReactStars
                    value={0}
                    count={5}
                    onChange={ratingChanged}
                    size={34}
                    isHalf={true}
                    emptyIcon={<FaRegStar />}
                    halfIcon={<FaStarHalf />}
                    fullIcon={<FaStar />}
                    activeColor="#ff6600"
                />
                <Title>Give your review a title</Title>
                <Input onChange={(e) => stateHandler('reviewTitle', e.target.value)} />
                <Title>Tell us about your experience</Title>
                <Input textarea onChange={(e) => stateHandler('reviewComment', e.target.value)} />
                {state.error && <p style={{color: 'red'}}>{state.error}</p>}
                <ButtonContainer>
                    <SubmitButton cancel onClick={() => onRequestClose()}>
                        Cancel
                    </SubmitButton>
                    <SendButton
                        title="Submit"
                        className="button"
                        loading={state.loading}
                        onClick={() => reviewAction()}
                    />
                </ButtonContainer>
            </div>
        </Container>
    );
}
