import React, { useState } from 'react';
import styled from 'styled-components';
import { FaRegStar, FaStar, FaStarHalf } from 'react-icons/fa';
import ReactStars from 'react-rating-stars-component';
import Modal from '@modules/Modal';
import AddReview from './AddReview';

import { colors } from '@components/typo';

const Container = styled.div`
    box-shadow: 3px 12px 24px 13px rgba(0, 0, 0, 0.03);
    width: 100%;
    padding: 10px;
    background: #fff;
    margin-top: 10px;
    display: flex;
    border-radius: 10px;
    cursor: pointer;
`;

const AvatarContainer = styled.div`
    height: 50px;
    width: 50px;
    border-radius: 30px;
    background: ${colors.grey};
`;

const Content = styled.div`
    flex: 1;
    margin-left: 15px;
`;

const Title = styled.div`
    font-weight: 700;
`;

export default function ReviewCard({data}) {
    const [openModal, setOpenModal] = useState(false);
    const ratingChanged = (newRating) => null
    return (
        <>
            <Container onClick={() => setOpenModal(true)}>
                <AvatarContainer></AvatarContainer>
                <Content>
                    <Title>Write Review</Title>
                    <ReactStars
                        value={0}
                        count={5}
                        onChange={ratingChanged}
                        size={24}
                        isHalf={true}
                        emptyIcon={<FaRegStar />}
                        halfIcon={<FaStarHalf />}
                        fullIcon={<FaStar />}
                        activeColor="#ff6600"
                    />
                </Content>
            </Container>
            <Modal visible={openModal} onRequestClose={() => setOpenModal(false)}>
                <AddReview data={data} onRequestClose={() => setOpenModal(false)} />
            </Modal>
        </>
    );
}
