import React from 'react';
import styled from 'styled-components';
import ReactTooltip from 'react-tooltip';

const Container = styled.div`
    padding: 5px;
    background-color: #e1f0ee;
    color: #125960;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    width: 125px;
    font-size: 11px;
    border-radius: 5px;
`;

const Content = styled.div`
    width: 300px;
`

export default function Badget() {
    return (
        <>
            <Container data-tip data-for="details">
                Verified Company
            </Container>
            <ReactTooltip id="details" place="bottom">
                <Content>
                    Transparency is important to us. To help people make better informed decisions, we display the
                    details this company has chosen to verify. Any company that has claimed their Trustpilot profile can
                    verify to get this badge. Learn more
                </Content>
            </ReactTooltip>
        </>
    );
}
