import React from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';
import { FaRegStar, FaStar, FaStarHalf } from 'react-icons/fa';
import ReactStars from 'react-rating-stars-component';

const Container = styled.div`
    box-shadow: 3px 12px 24px 13px rgba(0, 0, 0, 0.03);
    width: 100%;
    padding: 10px;
    background: #fff;
    margin-top: 10px;
    border-radius: 10px;
`;

const Header = styled.div`
    display: flex;
    align-items: center;
    border-bottom: 1px solid ${colors.grey};
    padding-bottom: 10px;
`;
const AvatarContainer = styled.div`
    height: 50px;
    width: 50px;
    border-radius: 30px;
    background: ${colors.grey};
`;
const UserContent = styled.div`
    flex: 1;
    margin-left: 15px;
`;
const Username = styled.div`
    font-weight: 700;
`;
const UserLocation = styled.div`
    color: ${colors.textColor2};
    font-size: 12px;
`;

const ContentTitle = styled.div`
    font-size: 16px;
    font-weight: 600;
    margin-top: 5px;
    margin-bottom: 5px;
`;
const Contents = styled.div``;

export default function ReviewFeed({data}) {
    return (
        <Container>
            <Header>
                <AvatarContainer></AvatarContainer>
                <UserContent>
                    <Username>{data.firstName} {data.lastName}</Username>
                    <UserLocation>{data.code3}</UserLocation>
                </UserContent>
            </Header>
            <Contents>
                <ContentTitle>{data.reviewTitle}</ContentTitle>
                <ReactStars
                    edit={false}
                    value={parseInt(data.reviewScore)}
                    count={5}
                    size={24}
                    isHalf={true}
                    emptyIcon={<FaRegStar />}
                    halfIcon={<FaStarHalf />}
                    fullIcon={<FaStar />}
                    activeColor="#ff6600"
                />
               {data.reviewComment}
            </Contents>
        </Container>
    );
}
